<template>
  <div>
    <Aside class="hidden-md-and-down" :slideStatus="slideStatus"/>
    <el-drawer
      :visible.sync="drawer"
      direction="ltr"
      size="200"
      :with-header="false">
        <Aside :slideStatus="false" @click="drawer = false"/>
      </el-drawer>
  </div>
</template>
<script>
import Aside from './Aside'
import { mapState, mapActions } from 'vuex'
export default {
  data () {
    return {
      drawer: false
    }
  },
  components: {
    Aside
  },
  computed: {
    ...mapState(['slideStatus'])
  },
  methods: {
    ...mapActions(['CHANGESLIDESTATUS'])
  },
  watch: {
    slideStatus () {
      if (window.innerWidth >= 1200) {
        return this.drawer = false
      }
      this.drawer = !this.drawer
    }
  }
}
</script>