<template>
  <el-aside :width="slideStatus ? '65px' : '200px'" class="_aside">
    <el-menu :collapse="slideStatus" default-active="/index" @select="handleSelect" class="el-menu-vertical-demo" router>
      <div class="logo_text" @click="handleGoHome">
        {{!slideStatus ? webUperText : webT}}
      </div>
      <div class="sub_text" v-show="!slideStatus">MENU</div>
      <el-menu-item index="/index">
        <i class="el-icon-s-home"></i>
        <span slot="title">Home</span>
      </el-menu-item>
      <el-submenu index="">
        <template slot="title">
          <i class="el-icon-user-solid"></i>
          <span slot="title">{{user_info ? user_info.username : 'User'}}</span>
        </template>
        <el-menu-item v-for="(item, i) in user_list" :index="item.index" :key="i">{{item.name}}</el-menu-item>
      </el-submenu>
      <div class="sub_text" v-show="!slideStatus">{{webUperText}}</div>
      <el-menu-item index="/about">
        <i class="el-icon-warning"></i>
        <span slot="title">About</span>
      </el-menu-item>
      <el-menu-item index="/service">
        <i class="el-icon-coin"></i>
        <span slot="title">Service</span>
      </el-menu-item>
      <el-submenu index="other">
        <template slot="title">
          <i class="el-icon-cpu"></i>
          <span slot="title">Other</span>
        </template>
        <el-menu-item index="/disciaimer">Disciaimer</el-menu-item>
        <el-menu-item index="/privacypolicy">Privacy Policy</el-menu-item>
      </el-submenu>
    </el-menu>
    <div class="btn_box" v-show="!slideStatus">
      <el-button type="primary" class="aside_btn"><i class="el-icon-upload el-icon--left"></i>Service</el-button>
      <el-button type="primary" class="aside_btn" plain><i class="el-icon-message el-icon--left"></i>12344556@qq.com</el-button>
    </div>
  </el-aside>
</template>
<script>
const User = [
  {
    index: 'login',
    name: 'login'
  },
  {
    index: 'register',
    name: 'register'
  }
]
let User2 = [
  {
    index: 'login',
    name: 'logout'
  }
]
import { mapState, mapActions } from 'vuex'
import { webUperText, webT } from '@/tool/commonText'
export default {
  props: {
    slideStatus: {
      type: Boolean
    }
  },
  data () {
    return {
      user_list: User,
      webUperText,
      webT
    }
  },
  mounted () {
    this.user_list = this.user_info ? User2 : User
  },
  methods: {
    handleGoHome () {
      this.$router.push('/')
    },
    handleSelect (index) {
      if (index === 'login' && this.user_info) {
        this.LOGOUT()
      }
      this.$emit('click')
    },
    ...mapActions(['LOGOUT'])
  },
  computed: {
    ...mapState(['user_info'])
  }
}
</script>
<style lang="less" scoped>
@import url('../../../assets/css/var.less');
._aside {
  transition: all .3s;
  overflow-x: hidden;
  max-height: 100vh;
  overflow-y: auto;
}
.logo_text {
  line-height: 50px;
  text-align: center;
  background: #fff;
  cursor: pointer;
}
.sub_text {
  padding-left: 10px;
  font-size: 12px;
  color: #a1a8ae;
}
.el-menu {
  border-right: none;
}
.btn_box {
  padding: 20px;
}
.aside_btn {
  width: 100%;
}
.el-button+.el-button {
  margin-left: 0;
  margin-top: 10px;
}
</style>
