<template>
  <div class="_header">
    <div class="header_nav">
      <div>
        <span class="i_size">
          <i class="el-icon-s-operation" @click="handleClick"></i>
        </span>
      </div>
      <div class="nav_right">
        <span class="el-icon-s-home i_size"></span>
        <span class="el-icon-message i_size"></span>
        <span class="el-icon-bell i_size"></span>
        <el-dropdown trigger="click" @command="handleDropdownClick">
          <span>
            <el-avatar class="img" v-if="!user_info" :size="40" src="https://lh3.googleusercontent.com/proxy/lmNNbpbJjmRnMSvvM24h93ksEJeDI0XeqIKBVsruzfz3ojZGeuSfmv3HEIY53h0tXzLkpq92te1YhOn4lRPokDAyGfxyNRwr9trqMkcHqkAJP5oY"></el-avatar>
            <span v-else>{{user_info.username}}</span>
            <!-- <img class="img" src="" alt=""> -->
            <span class="el-icon-caret-bottom"></span>
          </span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item v-for="(item, i) in dropList" :icon="item.icon" :key="i" :command="item.command">{{item.name}}</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
    </div>
  </div>
</template>
<script>
import { mapState, mapActions } from 'vuex'
let dropList = [
  {
    icon: 'el-icon-right',
    name: 'login',
    command: '/login'
  },
  {
    icon: 'el-icon-user',
    name: 'register',
    command: '/register'
  }
]
let dropList2 = [
  {
    icon: 'el-icon-right',
    name: 'logout',
    command: '/login'
  }
]
export default {
  mounted () {
    console.log(this.user_info)
    this.dropList = this.user_info ? dropList2 : dropList
  },
  data () {
    return {
      dropList: dropList
    }
  },
  methods: {
    handleClick () {
      this.CHANGESLIDESTATUS(!this.slideStatus)
    },
    handleDropdownClick (command) {
      if (this.user_info) {
        this.LOGOUT()
      }
      this.$router.push(command)
    },
    ...mapActions(['CHANGESLIDESTATUS', 'LOGOUT'])
  },
  computed: {
    ...mapState(['slideStatus', 'user_info'])
  }
}
</script>
<style lang="less" scoped>
@import url('../../../assets/css/var.less');
@nav_h: 60px;
._header {
  height: @nav_h;
  background: @color;
}
.i_size {
  font-size: 25px;
}

.header_nav {
  height: @nav_h;
  line-height: @nav_h;
  color: #fff;
  display: flex;
  justify-content: space-between;
  padding: 0 15px;
}
.nav_right {
  text-align: right;
  @mar_r: 25px;
  .img {
    vertical-align: middle;
    margin-right: 5px;
  }
  > span {
    margin-right: @mar_r;
    vertical-align: middle;
    &:last-child {
      margin-right: 0;
    }
  }
}
.el-dropdown {
  color: #fff;
}
.el-dropdown-menu__item {
  padding: 10px 18px;
}
</style>